import React from 'react';

export default function ItemDescribe({item}) {
    return (
        <div className="col-md-8">
            <div className="card-body product-item-description">
                <div className='product-item-description__header'>
                    <h5 className="card-title">{item.title}</h5>
                    <p className="card-text">{item.description}</p>
                </div>
                <p className="product-item-description__footer card-text">
                    <small className="text-muted">Last updated {Math.ceil(Math.random()*6) } days ago</small>
                </p>
            </div>
        </div>
    )
}